<template>
    <div class="main">
        <div class="drillManagement">
            <div class="searchA">
                <!-- 查询表单 -->
                <el-input v-model="formInline.keyword" size="small" clearable placeholder="请输入演练名称"></el-input>
                <el-select v-model="formInline.scene" size="small" clearable placeholder="请选择场景">
                    <el-option v-for="item in drillList" :label="item.dictionary_name" :value="item.dictionary_value" :key="item.dictionary_value"></el-option>
                </el-select>
                <el-button type="primary" size="small" @click="search()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" icon="" @click="addMan">新增演练</el-button>
                </div>
            </div>
            <!-- 新增按钮 -->
            <!-- 表格 -->
            <div class="home_table">
                <el-table :data="tableData" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" v-if="item.prop=='people'" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                <el-link @click="peopleClick(scope.row)" :underline="false" target="_blank" type="primary">查看任务</el-link>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" v-else-if="item.prop=='operation'" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                <el-link :underline="false" target="_blank" type="primary" @click="operationClick(scope.row)" :style="{color:scope.row.status==1?'#ff2b2b':''}">{{scope.row.status==0?'开始演练':'停止演练'}}</el-link>
                            </template>
                        </el-table-column>
                        <el-table-column v-else :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                        </el-table-column>
                    </template>
                    <el-table-column fixed="right" label="操作" width="120">
                        <template slot-scope="scope">
                            <el-button @click="handleClickEdit(scope.row)" type="text" size="small">编辑</el-button>
                            <el-button @click="handleClickDel(scope.row.id)" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!-- 分页 -->
            <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
            </div>
        </div>

        <!-- 查看/编辑弹层 -->
        <el-dialog :title="title" :visible.sync="centerDialogVisible" width="60%" center>
            <el-form :model="ruleForm" :disabled="checked" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="演练名称" prop="title">
                    <el-input v-model="ruleForm.title" style="width:300px" placeholder="请输入演练名称"></el-input>
                </el-form-item>
                <!-- 人员任务开始 -->
                <el-form-item label="人员任务" :rules="rules" v-for="(item) in ruleForm.list" :key="item.id">
                    <el-select v-model="item.real_name" placeholder="请选择人员">
                        <!-- <el-option :label="item.real_name" :value="shanghai"></el-option> -->
                        <el-option label="正常演练" value="1"></el-option>
                        <el-option label="地震预警" value="2"></el-option>
                    </el-select>
                    <el-input v-model="item.task" style="width:300px" placeholder="请输入人员任务内容"></el-input>
                    <el-button type="primary" plain @click.prevent="removeDomain(domain)">删除</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain @click="addDomain">添加</el-button>
                </el-form-item>
                <!--人员任务结束  -->

                <el-form-item label="场景" prop="scene_name">
                    <el-select v-model="ruleForm.scene_name" placeholder="请选择场景">
                        <el-option label="正常演练" value="1"></el-option>
                        <el-option label="地政预警" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item style="text-align:right;margin-right:20px" v-if="!checked">
                    <el-button type="primary" @click="submitForm">保存</el-button>
                    <el-button type="primary" @click="centerDialogVisible=false">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog title="人员任务" :visible.sync="dialogVisible" v-dialogDrag width="700px" :close-on-click-modal="false">
            <div class="home_table">
                <el-table :data="tableDataTask" border element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHeadTask">
                        <el-table-column :prop="item.prop" v-if="item.prop=='status'" align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true">
                            <template slot-scope="scope">
                                {{scope.row.status==1?"已确定":"未确定"}}
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" v-else align="center" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    </template>
                </el-table>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { hashApi, drillListApi, viewTaskApi, viewTaskDelApi, drillStaEnd, drillManagementEditApi, drillManagementAddApi, drillView } from "@/api/index"
export default {

    data() {
        return {
            xdebug: "",
            keyword: "",
            scene: "",
            total: 0,
            page: 1,
            pageSize: 15,
            tableData: [],
            tableHeight: window.innerHeight - 180,
            status: "",
            hash: "",
            dialogVisible: false,
            centerDialogVisible: false,//弹层得初始状态
            title: '',//新增编辑标题
            checked: false,//点击的是查看/编辑状态
            ruleForm: {
                id: "",
                list: [{
                    user_id: "",
                    task: ""
                }],
                scene: "",
                scene_name: '',
                title: "",
            },
            rules: {
                scene_name: [
                    { required: true, message: '请输入活动名称', trigger: 'blur' },

                ],
                region: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],
                title: [
                    { required: true, message: '请选择活动区域', trigger: 'change' }
                ],

            },
            deviceType: [],//查看任务类型列表
            ifStartMan: true,
            startManText: "开始演练",
            formInline: {
                keyword: '',
                scene: ''
            },
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '80' },
                { title: '演练名称', prop: 'title', minWidth: '160' },
                { title: '场景', prop: 'scene_name', minWidth: '120' },
                { title: '人员任务', prop: 'people', minWidth: '120' },
                { title: '创建时间', prop: 'create_time', minWidth: '160' },
                { title: '演练操作', prop: 'operation', minWidth: '120' },
            ],
            tableHeadTask: [
                { title: '序号', prop: 'id', minWidth: '80', },
                { title: '参与人员', prop: 'real_name', minWidth: '100', },
                { title: '人员任务', prop: 'task', minWidth: '100', },
                // { title: '演练确定', prop: 'status', minWidth: '100', },
            ],
            tableDataTask: [],
            task: {},
            drillList: [],//演练类型
        }
    },

    created() {
        this.drillGetList()
        this.getHash();
        this.resView();
    },
    watch: {
        centerDialogVisible(val) {
            if (val) {
                this.title === '编辑' ? this.getInfo() : ''
            }
        }
    },
    methods: {
        search() {
            this.page = 1;
            this.drillGetList();
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.drillGetList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.drillGetList();
        },
        //资源
        async resView() {
            const res = await this.axios.post(drillView);
            if (res.data.code == 1) {
                this.drillList = res.data.data.scene_list
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //人员任务
        peopleClick(row) {
            this.dialogVisible = true;
            this.tableDataTask = [];
            this.task = {};
            this.$nextTick(() => {
                this.taskInfo(row.id)
            })
        },
        //人员任务
        async taskInfo(id) {
            const res = await this.axios.post(viewTaskApi, { id: id });
            if (res.data.code == 1) {
                this.tableDataTask = res.data.data.list;
                this.task = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //开始停止演练
        async operationClick(row) {
            let data = {
                id: row.id,
                state: row.status == 1 ? 0 : 1,
                hash: this.hash,
            }
            const res = await this.axios.post(drillStaEnd, data);
            if (res.data.code == 1) {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                });
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.getHash();
            this.drillGetList();
        },
        //编辑删除
        removeDomain(item) {
            var index = this.ruleForm.list.indexOf(item)
            if (index !== 0) {
                this.ruleForm.list.splice(index, 1)
            }
        },

        //弹层-提交按钮
        submitForm() {
            this.$refs['ruleForm'].validate(async valid => {
                if (valid) {
                    await this.getHash();
                    let data = {
                        XDEBUG_SESSION_START: this.xdebug,
                        id: this.ruleForm.id,
                        title: this.ruleForm.title,
                        scene: this.ruleForm.scene,
                        task_data: JSON.stringify(this.ruleForm.list),
                        hash: this.hash,
                    }
                    const res = await this.axios.post(drillManagementEditApi, data);
                    if (res.data.code == 1) {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'success'
                        });
                        this.centerDialogVisible = false;
                        await this.drillGetList()
                    } else {
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error'
                        });
                    }

                }
            })
        },

        //添加人员和任务
        addDomain() {
            this.ruleForm.list.push({
                user_id: '',
                task: "",
            });
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'drillManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },

        //获取列表
        async drillGetList() {
            let data = {
                keyword: this.formInline.keyword,
                scene: this.formInline.scene,
                curr: this.page,
                pageSize: this.pageSize,
            }
            const res = await this.axios.post(drillListApi, data);
            if (res.data.code == 1) {
                this.total = res.data.data.total
                this.tableData = res.data.data.data
            }
            else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                })
            }

        },
        //查询清空
        reset() {
            this.formInline = {
                name: "",
                scene: ""
            }
            this.$nextTick(() => {
                this.$refs.formInline.resetFields()
            })
            this.drillGetList()
        },

        //新增演练
        addMan() {
            localStorage.removeItem('Rehearsal_Id');
            localStorage.setItem('type', 'add');
            // console.log("新增演练");
            this.$router.push({ name: "drillManAdd" })
        },


        //操作-删除
        handleClickDel(id) {
            this.getHash()
            this.$confirm('是否确定删除该设备', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(async () => {
                    const data = {
                        XDEBUG_SESSION_START: this.xdebug,
                        id: id.toString(),
                        hash: this.hash
                    };
                    const res = await this.axios.post(viewTaskDelApi, data).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            //渲染表单
                            this.drillGetList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }

                    });
                })
                .catch(err => {
                    this.$notify.error({
                        title: '失败',
                        message: '取消操作'
                    });
                });
        },
        //
        async getInfo(row) {
            let data = {
                XDEBUG_SESSION_START: this.xdebug,
                id: row.id
            }
            const res = await this.axios.post(viewTaskApi, data)
            // console.log("res-viewTask", res);
            if (res.data.code == 1) {
                this.ruleForm = res.data.data
                // this.deviceType = res.data.data.list
            }
            else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                })
            }
        },

        // 查看任务-按钮
        viewTask(row) {
            // console.log("查看任务", row);
            this.title = '查看任务';
            this.checked = true;
            this.centerDialogVisible = true;
            this.resetForm(); //把表单重置
            this.getInfo(row)
        },
        //表单重置
        resetForm() {
            this.ruleForm = {
                id: "",
                list: [{
                    user_id: "",
                    task: ""
                }],
                scene: "",
                scene_name: '',
                title: "",
            },
                this.$nextTick(() => {
                    this.$refs['ruleForm'].resetFields();
                })
        },

        //编辑按钮
        async handleClickEdit(row) {
            localStorage.setItem('Rehearsal_Id', JSON.stringify(row.id))
            localStorage.setItem('type', 'edit');
            this.$router.push({
                name: "drillManAdd", params: {
                    id: row.id,
                }
            })

        },
    }

}
</script>

<style lang="scss" scoped>
.drillManagement {
    .ql-editor {
        padding: 0;
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 0px;
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
    }
    .btn {
        width: 250px;
    }
}
</style>


